import React from 'react'
import Link from 'gatsby-link'

const SecondPage = () => (
  <div>
    <pre>
        <h3>አቤት ማማሯ </h3>
        <p>
          የናቱዬ ድመት አቤት ማማሯ<br />
          በቀለማት ተዥጎርጉራ ተዥጎርጉራ። <br /><br />

          ፀሐይ የመሰለ ቢጫ ፊት ነው ያላት<br />
          ከቀይ ጢሟ ጋር በጣም ነው የሚያምርባት።<br /><br />

          እንደ ውቅያኖስ ውሃ ኩልል ጥርት ያሉ<br />
          ሰማያዊ አይኖችዋ ከብለል ከብለል ሲሉ<br />
          ያላፊ አግዳሚውን ልብ ይማርካሉ።<br /><br />

          አረንጓዴ ጆሮዎቿ ግራ ቀኙን ይዘው<br />
          እንደ አንቴና ተወጥረው<br />
          አናቷ ላይ የበቀሉ<br />
          ቅጠል ነገር ይመስላሉ።<br /><br />


          በጀርባዋ በኩል ካንገቷ ጀምሮ<br />
          እስከ ታች ድረስ ጭራዋን ጨመሮ<br />
          ቀለሟ ጥቁር ነው<br />
          ጃኖ ነው የሚመስለው። <br /><br />

          በሆዷ በኩል ግን ገልብጠን ስናያት<br />
          ጥጥ የመሰለ ነው ነጭ ፀጉር ያላት።<br /><br />

          ቡኒ እግሮቿን .. ለተመለተው<br />
          ሕብረ ቀለሙ እጅግ .. የሚማርክ ነው<br />
          ስትዘል ስትቧርቅ እያንጸባረቀ<br />
          የስንቱን ተመልካች ልቦና ሰረቀ።<br /><br />

          የናቱዬ ድመት አቤት ማማሯ<br />
          በቀለማት ተዥጎርጉራ ተዥጎርጉራ።<br />
        </p>
    </pre>
    
    <Link to="/">መጀመሪያ</Link> | 
    <Link to="/page-3/">ወደ ሚቀጥለው</Link>
  </div>
)

export default SecondPage



